<script>
  import api from 'src/packages/Api'
  import Loading from 'partials/Loading'
  
  export default {
    name: 'update-career-expertise',
    data () {
      return {
        expertise: {},
        title: '',

        loaded: false        
      }
    },
    components: {
      Loading
    },
    created () {
      api.get(`careers/expertises/${this.$route.params.id}`).then(response => {
        const res = response.data

        this.expertise = res
        this.id = res.id
        this.title = res.title

        this.loaded = true
      }).catch(error => {
        this.$store.dispatch('addToastMessage',
          { text: this.$t(`errors.${error.data.status_code}`) }
        )
        this.$router.push({ name: 'careers-expertises' })
      })
    },
    methods: {
      update () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            const data = { title: this.title }

            api.put(`careers/expertises/${this.id}`, data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('career.expertise.updated'), type: 'success' }
              )
              this.$router.push({ name: 'careers-expertises'})
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: this.$t('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

<template>
  <div class="form-elements">
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.careers.expertises.update' | translate">
          <div class="row row-reverse">
            <div class="col-6">
              <div class="row row-reverse">
                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('title')}">
                    <div class="input-group">
                      <input
                        id="title"
                        name="title"
                        v-model="title"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('title')"></i>
                      <label class="control-label" for="title">{{'career.expertise.title'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('title')"
                             class="help text-danger">{{
                        errors.first('title')
                        }}
                      </small>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col mt-5 text-center">
              <button class="btn btn-success"  @click="update">
                {{'buttons.update' | translate}}
              </button>
            </div>
          </div>          
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>
